import { message } from 'antd'
import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Button, Container, Modal, Row, Table } from 'react-bootstrap'
import ReactPaginate from 'react-paginate'

const AllUserComplaints = () => {
  const [show, setShow] = useState(false)
  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)
  const [userComplaints, setUserConplaints] = useState([])
  const [pageCount, setPageCount] = useState(1)
  const limit = 8
  const currentPage = useRef(1)
  const getPaginatedData = async (page) => {
    try {
      const res = await axios.get(
        `/api/v1/masterAdmin/getUserComplaints?page=${currentPage.current}&limit=${limit}`,
      )
      if (res.data.success) {
        const data = res.data.data
        if (data.results.length === 0) {
          setUserConplaints([])
        } else {
          setUserConplaints(data.results)
          setPageCount(data.totalPages) // Use total pages from the response
        }
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }

  const handlePageClick = (e) => {
    const selectedPage = e.selected + 1
    currentPage.current = selectedPage
    getPaginatedData(selectedPage) // Fetch data for the selected page
  }
  useEffect(() => {
    currentPage.current = 1
    getPaginatedData()
  }, [])

  return (
    <>
      <Row className="mb-3">
        <Container fluid>
          <h3>Contact Details of users</h3>
          <Row>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">Sn</th>
                  <th className="text-center">First Name</th>
                  <th className="text-center">UniqueId</th>
                  <th className="text-center">Mobile Number</th>
                  <th className="text-center">Title</th>
                  <th className="text-center">Ticket ID</th>
                  <th className="text-center">Action</th>
                </tr>
              </thead>
              <tbody>
                {userComplaints && userComplaints.length > 0 ? (
                  userComplaints.map((item, index) => (
                    <>
                      <tr key={index}>
                        <td className="text-center">{index + 1}</td>
                        <td className="text-center">{item.firstName}</td>
                        <td className="text-center">{item.userId}</td>
                        <td className="text-center">{item.mobile}</td>
                        <td className="text-center">{item.complaintTitle}</td>
                        <td className="text-center">{item.ticketId}</td>
                        <td className="text-center">
                          <Button onClick={handleShow} className="myCustomBtn">
                            View More
                          </Button>
                        </td>
                      </tr>
                      <Modal show={show} onHide={handleClose} animation={false}>
                        <Modal.Header closeButton>
                          <Modal.Title>{item.complaintTitle}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>{item.complaintMessage}
                        <div className="bg-black d-flex justify-content-center align-items-center"
                        style={{
                          width: "100%",
                          height: "10rem",
                          opacity: "0.2"
                          
                        }}
                        >
                        <img src={item.screenshot} alt=""  style={{
                          width: "100%",
                          height: "100%",
                          objectFit: "contain"
                        }}/>
                        </div>
                        </Modal.Body>
                        <Modal.Footer>
                          <Button className='myCustomBtn' onClick={handleClose}>
                            Set as Resolved
                          </Button>
                        </Modal.Footer>
                      </Modal>
                    </>
                  ))
                ) : (
                  <tr>
                    <td colSpan={8} className="text-center text-danger">
                      No data found
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </Row>
        </Container>
      </Row>
      <Row>
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={pageCount}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          marginPagesDisplayed={2}
          containerClassName="pagination justify-content-center"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          activeClassName="active"
        />
      </Row>
    </>
  )
}

export default AllUserComplaints
